import { Component, OnInit } from '@angular/core';
import { async } from '@angular/core/testing';
import { HttpClient } from '@angular/common/http'

@Component({
  selector: 'weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.scss']
})
export class WeatherComponent{
 
  response:object;
  weather:object;
  weatherIcon:string;


  constructor(private http: HttpClient) { 
    this.weather = {
      clearsky: '01',
      fewclouds: '02',
      scatteredclouds: '03',
      brokenclouds: '04',
      overcastclouds: '04',
      showerrain: '09',
      rain: '10',
      thunderstorm: '11',
      snow: '13',
      mist: '50'
    }
  }

  getIconFromId (id){
    if(id > 199 && id < 233) return '11';
    if(id > 299 && id < 322) return '09';
    if(id > 499 && id < 505) return '10';
    if(id == 511) return '13';
    if(id > 519 && id < 532) return '09';
    if(id > 599 && id < 623) return '09';
    if(id > 700 && id < 782) return '09';
    if(id == 800) return '01';
    if(id > 800 && id < 805) return '04';
  }
  

  getWeather($event, cityName){
    
    $event.preventDefault();
    if(!cityName.value) {
      alert('Please enter city name');
    }
    const test = this.http.get(`https://api.openweathermap.org/data/2.5/weather?q=${cityName.value}&appid=fec8b249edbf6232ae4e5957bd8e7ecf&units=metric`)
    .subscribe(res=>{
      this.response = res;
      if(res.hasOwnProperty('weather')) 
      {
        let weatherIcon = this.getIconFromId(parseInt(res['weather'][0].id));
        console.log(this.getIconFromId(804));
        res['dt'] < res['sys'].sunrise || res['dt'] > res['sys'].sunset ? weatherIcon += 'n' : weatherIcon += 'd';
        weatherIcon = `http://openweathermap.org/img/w/${weatherIcon}.png`;
        this.weatherIcon = weatherIcon;
        
      }
    }, (err) => {return alert('City not found or server disconnected')})
  }
}

export default interface Response {}

